export interface ImpactOptions {
  baseUrl: string;
  user: string;
  password: string;
  groupName: string;
  callbackUrl: string;
  initializeDevices: boolean;
}

export interface TenantConnectionStatusResponse {
  status: TenantConnectionStatus;
  options: ImpactOptions;
}

export enum TenantConnectionStatus {
  NO_CONNECTION = 'NO_CONNECTION',
  CONNECTING_IN_PROGRESS = 'CONNECTING_IN_PROGRESS',
  CONNECTED_SUCCESSFULLY = 'CONNECTED_SUCCESSFULLY',
  CONNECTED_FAILED = 'CONNECTED_FAILED',
  UNKNOWN = 'UNKNOWN'
}
