import { Injectable } from '@angular/core';
import { FetchClient, IFetchOptions, IFetchResponse } from '@c8y/client';
import { ImpactOptions } from './impact.model';

@Injectable()
export class ImpactConnectivityService {
  private readonly microserviceUrl: string = '/service/impact/connectivity';
  private readonly header: any = { 'Content-Type': 'application/json' };

  constructor(private client: FetchClient) {}

  getOptions(): Promise<IFetchResponse> {
    const options: IFetchOptions = {
      method: 'GET',
      headers: this.header
    };
    return this.client.fetch(`${this.microserviceUrl}`, options);
  }

  updateOptions(impactOptions: ImpactOptions): Promise<IFetchResponse> {
    const options: IFetchOptions = {
      method: 'POST',
      headers: this.header,
      body: JSON.stringify(impactOptions)
    };
    return this.client.fetch(`${this.microserviceUrl}`, options);
  }

  deleteOptions(): Promise<IFetchResponse> {
    const options: IFetchOptions = {
      method: 'DELETE',
      headers: this.header
    };
    return this.client.fetch(`${this.microserviceUrl}`, options);
  }
}
