import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { IFetchResponse } from '@c8y/client';
import { AlertService, gettext, ModalService, Status } from '@c8y/ngx-components';
import { ImpactConnectivityService } from './impact-connectivity.service';
import {
  ImpactOptions,
  TenantConnectionStatus,
  TenantConnectionStatusResponse
} from './impact.model';

@Component({
  selector: 'c8y-impact-provider-settings',
  templateUrl: './impact-provider-settings.component.html'
})
export class ImpactProviderSettingsComponent implements OnInit {
  connectionStatus: TenantConnectionStatusResponse;
  formGroup: FormGroup;

  isEdit: boolean = false;
  credentialsExist: boolean = false;

  constructor(
    private impactService: ImpactConnectivityService,
    private formBuilder: FormBuilder,
    private modal: ModalService,
    private alert: AlertService
  ) {}

  async ngOnInit() {
    this.initForm();
    const response: IFetchResponse = await this.impactService.getOptions();
    this.connectionStatus = await response.json();

    if (this.connectionStatus && this.connectionStatus.options) {
      this.formGroup.patchValue({ ...this.connectionStatus.options });
      this.credentialsExist = true;
    } else {
      this.isEdit = true;
    }
  }

  replaceCredentials(): void {
    this.isEdit = true;
  }

  async saveCredentials() {
    if (this.formGroup.valid) {
      this.connectionStatus.status = TenantConnectionStatus.CONNECTING_IN_PROGRESS;
      const updated: boolean = await this.safelyUpdateCredentials(this.formGroup.value);

      if (updated) {
        const response: IFetchResponse = await this.impactService.getOptions();
        this.connectionStatus = await response.json();

        if (this.connectionStatus.status === TenantConnectionStatus.CONNECTED_SUCCESSFULLY) {
          this.isEdit = false;
        }

        this.alert.success(gettext('Credentials saved.'));
      } else {
        this.connectionStatus.status = TenantConnectionStatus.UNKNOWN;
      }
    }
  }

  async deleteCredentials() {
    try {
      await this.modal.confirm(
        gettext('Delete credentials'),
        gettext(
          'You are about to delete your IMPACT credentials. Deleting credentials will break connection to IMPACT instance. Do you want to proceed?'
        ),
        Status.DANGER,
        { ok: gettext('Delete'), cancel: gettext('Cancel') }
      );
      await this.safelyDeleteCredentials();
    } catch (ex) {
      // Intentionally empty
    }
  }

  private initForm(): void {
    this.formGroup = this.formBuilder.group({
      baseUrl: [],
      user: ['', Validators.required],
      password: ['', Validators.required],
      groupName: [],
      initializeDevices: [false]
    });
  }

  private resetForm(): void {
    this.formGroup.reset();
  }

  private async safelyUpdateCredentials(options: ImpactOptions): Promise<boolean> {
    try {
      const res = await this.impactService.updateOptions(options);
      if (res && res.status !== 200) {
        const data = res.json ? await res.json() : undefined;
        this.alert.addServerFailure({ data, res });

        return Promise.resolve(false);
      } else {
        return Promise.resolve(true);
      }
    } catch (ex) {
      this.alert.addServerFailure(ex);

      return Promise.resolve(false);
    }
  }

  private async safelyDeleteCredentials() {
    try {
      const res = await this.impactService.deleteOptions();
      if (res && res.status !== 200) {
        const data = res.json ? await res.json() : undefined;
        this.alert.addServerFailure({ data, res });
      } else {
        this.credentialsExist = false;
        this.resetForm();
        this.connectionStatus = null;
        this.alert.success(gettext('Credentials deleted.'));
      }
    } catch (ex) {
      this.alert.addServerFailure(ex);
    }
  }
}
